html,body {
  margin: 0;
  padding: 0;
  max-width: 100vw;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color:   #161616
}

.App-header{
  background: url(/Linux-website/static/media/desk.b285c4f8.jpg) no-repeat center center fixed;
  background-size: cover;
  display: table;
  width: 100%;
  height: 100vh;
  position: relative;
}
.landing-text {
  display: table-cell;
  text-align: center;
  vertical-align: middle; 
}
h1{
  font-size: 310%;
}
.btn{
  background:  #161616;
  border: 1px solid #7c818f;
  border-radius: 12px;
  padding: 1%;
  text-transform: uppercase;
  color: white;
}
a {
  text-decoration: none;
  color:  white;
}
a:visited, a:link{
  text-decoration: none;
}
a:hover{
  color: #fffff0;
}
.AboutLinux{
  padding: 3%;
  text-align: left;
  min-height: 100vh;
  max-width: 100%;
  margin: 1%;
  
}
.AboutLinux h2{
  text-align: center;
}
.foot{
  height: 40vh;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  background: #161616;
  word-wrap: break-word;
  white-space: pre-line;
  color: white;
  line-height: 40vh;

}
.commends{
  padding:1%;
 background: #f1f4f7;
 display:flex;
 flex-direction: column;
 align-items: center;
}
.commends ul{
  border: solid 0.5px #123456;
  margin-left: 15%;
  margin-right: 15%;
}
.up{
  color: #123456;
}
.commends a,
.commends p,
.pinkClass p,
.foot i{
  color: #e7025f !important;
}
.FloatingActionButtonZoom-root-1{
  width: 70% !important;
  max-height: 100%;
  word-wrap: break-word;
}
@media (max-width: 768px){
  .App-header{
    background:#f1f4f7;
    width: 100%;
    height:40vh;}
    h1, h2{
    font-size: 100%;
  }
 h3{
    font-size:90%;
}

 .AboutLinux{
    padding:0;
    word-wrap: break-word;
    white-space: pre-line;
    margin: 0;
    max-height: 100vh;
  }
  .commends ul{
    border: solid 0.5px #123456;
    margin: 0;
  }
  .foot{
    font-size: 50%;
    height: 10vh;
    line-height: 10vh;
  }
  .FloatingActionButtonZoom-root-1{
    width: 100% !important;
    font-size: 90%;
  }

}

.ten{
  text-indent: 10%
}
.Bash{
  text-align: left
}
.link{
  background: #f1f4f7;
  margin:0;
  padding: 2%
}
.toolbar{
    position: relative;
    width: 100%;
    background: #161616;
    color: white;
    margin:0;
    height: 10vh;

}
.toolbar__navigation{
    display: flex;
    height: 100%;
    padding: 0 1rem;
    align-items: center;
}
.spacer{
    flex: 1 1;
}
.toolbar_items ul{
    list-style:  none;
    margin: 0;
    padding: 0;
    display: flex;
}
.toolbar_items li{
    padding: 0 0.5rem;
}
.toolbar_items a{
    color: white;
    text-decoration: none;
}
.toolbar_items a:hover,
.toolbar_items a:active {
    color: #fffff0;
}
@media (max-width: 768px) {
    .toolbar_items{
        display: none;
    }
}
@media (min-width: 769px){
    .toolbar__toggle{
        display: none;
    }
}
.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}
.toggle-button:focus{
    outline: none;
}
.toggle-button_line{
    width: 30px;
    height: 2px;
    background: white;
}
.side-drawer{
    height: 100%;
    background: white;
    position: fixed;
    top: 0;
    left: 0;
    width: 60%;
    max-width: 400px;
    z-index: 200;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    -webkit-transform:  translateX(-100%);
            transform:  translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
.side-drawer.open {
    -webkit-transform:  translateX(0);
            transform:  translateX(0);
}
.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-drawer li{
    margin: 0.5rem 0;
}
.side-drawer a{
    color: black;
    text-decoration: none;
    font-size:1.2rem;
}
.side-drawer a:hover,
.side-drawer a:active{
    color: #fffff0;
}

@media (min-width: 769px){
    .side-drawer{
        display: none;
    }
}
.backdrop{
    position: fixed;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
